/* =========================================styles for the components========================================================== */

body {
  font-family: Work Sans;
}

/* =========================================styles for  App component=================================================== */

.innerdiv {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.headimage {
  height: 50px;
  width: 264px;
}

#welcome-text {
  font-size: 1.8rem;
  display: inline-block;
  margin-top: 90px;
}

#main-form {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 34px;
}

.info-text {
  font-size: 0.8rem;
  color: darkgray;
  margin-top: 6px;
}

.info-text2 {
  font-size: 0.8rem;
  color: darkgray;
}

#mainform-submit {
  border: 0;
  background-color: #f7b348;
  color: white;
  border-radius: 18px;
  height: 36px;
  width: 220px;
  margin-top: 116px;
}

#signin-text {
  color: #666;
  margin-top: 14px;
}

.number-input {
  position: absolute;
  left: 90px;
  top: 15px;
  height: 32px;
  border: 0px;
  width: 256px;
}

#numberinput {
  margin-top: 20px;
  margin-bottom: 20px;
  position: relative;
  /* border: 1px solid black; */
  height: 50px;
  width: 350px;
}

#numberinputlabel {
  position: absolute;
  font-size: 0.8rem;
  color: darkgray;
  left: 30px;
}

.my-flag {
  position: absolute !important;
  left: 16px;
  top: 7px;
}

#left-block {
  position: absolute;
  height: 44px;
  border: 1px solid #ffd37d;
  top: 9px;
  width: 14px;
  border-right: 0px;
  left: 12px;
  border-top-left-radius: 6px;
  border-bottom-left-radius: 6px;
}

#right-block {
  position: absolute;
  border: 1px solid #ffd37d;
  height: 43px;
  width: 176px;
  top: 10px;
  left: 174px;
  border-left: 0px;
  border-top-right-radius: 6px;
  border-bottom-right-radius: 6px;
}

#bottom-block {
  position: absolute;
  border-bottom: 1px solid #ffd37d;
  height: 0;
  width: 148px;
  top: 52px;
  left: 26px;
}

/* ===============================================styles for AskOtp component=========================================== */

#mainimage-container {
  margin-top: 60px;
}

#otpsentimage {
  margin-top: 45px;
}

#otp-form {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

#verify-text {
  margin-top: 48px;
  font-size: 1.3rem;
}

#phone-number-text {
  margin-top: 33px;
  color: #8c8c8c;
}

#changenumberbutton {
  border: 0;
  background: none;
  color: #f7b348;
  font-size: 14px;
  text-decoration: underline;
}

.boxes {
  height: 48px;
  width: 48px;
  border-radius: 5px;
  border: 1px solid gainsboro;
  margin-right: 20px;
  text-align: center;
  font-size: 22px;
}

#box1 {
  margin-left: 24px;
}

.digitbox {
  margin-top: 90px;
}

#resend-text {
  margin-top: 82px;
  display: inline-block;
  color: #a39f9f;
}

#verify-button {
  display: block;
  border: 0;
  background-color: #f7b348;
  height: 40px;
  margin-top: 25px;
  width: 210px;
  border-radius: 20px;
  color: white;
}

#resend-button {
  border: 0;
  background: none;
  color: #f7b348;
  margin-left: 18px;
}

/* =========================================styles for OtpSuccess component============================================ */

#imagecontainer2 {
  position: relative;
}

#innerBoxContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
}

#imagepart2 {
  position: absolute;
  top: 265px;
  right: -15px;
}

#welcome-text2 {
  margin-top: 36px;
  font-size: 1.3rem;
  font-weight: 600;
}

#line-text1 {
  margin-top: 16px;
  color: #aca6a6;
}

#line-text2 {
  color: #aca6a6;
}

#line-text3 {
  font-weight: 600;
  color: #666666;
}

#getstartedbutton {
  margin-top: 85px;
  border: 0;
  background: #f7b348;
  height: 40px;
  width: 154px;
  border-radius: 20px;
  color: white;
  font-weight: 500;
}

#last-text {
  font-size: 0.9rem;
  color: gray;
}

#innerphonenumber {
  color: black;
}
